"use strict";
var Paging = (function () {
    function Paging(category) {
        var id = 'postsPaging' + category;
        this.pagingElem = document.getElementById(id);
        if (!this.pagingElem) {
            this.pagingElem = this.createNewPagingElem(id);
        }
    }
    Paging.prototype.createNewPagingElem = function (id) {
        var div = document.createElement('div');
        div.id = id;
        div.className = 'postsPaging';
        div.innerHTML = 'weitere Posts laden';
        document.getElementById('pagingContainer').appendChild(div);
        return div;
    };
    Paging.prototype.showPaging = function () {
        this.pagingElem.style.display = 'block';
    };
    Paging.prototype.hidePaging = function () {
        this.pagingElem.style.display = 'none';
    };
    Paging.prototype.processPaging = function (currentPages, totalPages) {
        var pagingActive = Number(currentPages) < Number(totalPages);
        if (pagingActive) {
            this.showPaging();
        }
        else {
            this.hidePaging();
        }
        return pagingActive;
    };
    return Paging;
}());
module.exports = Paging;



